/**
 * @description
 * Clears the Spaces before and after the string
 *
 * @example
 * ``` ts
 * strTrim('  foo  ');
 * // => foo
 * strTrim('\tfoo\t');
 * // => foo
 * strTrim(' F R ED ');
 * // => F R ED
 * ```
 *
 * @param str The string to be converted
 *
 * @docsCategory preference/utils/string
 * @codeFilter str-trim
 */
export const strTrim = (str: string) => {
  return (str && str.replace(/^\s+|\s+$/g, '')) || '';
};
